import React from 'react';
import { Link } from 'react-router-dom';

const Navbar = () => {
  return (
    <nav className="navbar">
      <div className="logo">
        <h1>V S Protocal</h1>
      </div>
      <div className="nav-menu">
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/singin">Sign In</Link>
          </li>
          <li>
            <Link to="/singin">lIGHT</Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
