import React from 'react';
import Barcode from 'react-barcode';
import { useState } from 'react';

const BarcodeLabels = () => {
  // form DB
  const [cType, setcType] = useState(0);
  const [cartId, setcartId] = useState(0);
  const [nmLabels, setnmLabels] = useState(0);
  const [barcodeArray, setbarcodeArray] = useState([]);

  // global DB

  const MakeBTN = () => {
    if (cType == '' && cartId == '' && nmLabels < 13) {
      alert('Pleaee Enter Balite Data');
    } else {
      BarcodeMaker();
    }
  };

  const BarcodeMaker = () => {
    let PushArrayData = [];
    var BarcodeLables = 0;
    var BarcodecartType = '';
    var barcodeTypePositionforstring = 'R';
    var barcodeTypePositionfornumber = 0;

    if (cType == 1) {
      BarcodecartType = 'S';
      BarcodeLables = 12;
    }
    if (cType == 2) {
      BarcodecartType = 'D';
      BarcodeLables = 8;
    }
    if (cType == 3) {
      BarcodecartType = 'T';
      BarcodeLables = 6;
    }

    if (nmLabels != 0) {
      console.log('yes');
      BarcodeLables = nmLabels;
    }

    PushArrayData.push(`${BarcodecartType}-${cartId}`);

    for (var i = 1; i <= BarcodeLables; i++) {
      barcodeTypePositionfornumber++;

      if (cType == 1) {
        if (barcodeTypePositionfornumber > 6) {
          barcodeTypePositionfornumber = 0;
          barcodeTypePositionfornumber++;
          barcodeTypePositionforstring = 'L';
        }
      }

      PushArrayData.push(`${BarcodecartType}${cartId}${barcodeTypePositionforstring}-0${barcodeTypePositionfornumber}`);
    }

    console.log(BarcodeLables, BarcodecartType, nmLabels);
    setbarcodeArray(PushArrayData);
    return 0;
  };

  return (
    <>
      <div className="CartBarcodeMain">
        <div className="form-block">
          <div className="input">
            <select
              onChange={e => {
                setcType(e.target.value);
              }}
              className="uneditable-input"
            >
              <option value="" hidden>
                Select Cart Type
              </option>
              <option value="1">Single</option>
              <option value="2">Double</option>
              <option value="3">Triple</option>
            </select>
          </div>

          <div className="input">
            <input
              type="text"
              onChange={e => {
                setcartId(e.target.value);
              }}
              placeholder="Enter Cart ID"
            />
          </div>

          <div className="input">
            <input
              type="text"
              onChange={e => {
                setnmLabels(e.target.value);
              }}
              placeholder="Number Of Label"
            />
          </div>

          <div className="input action">
            <button type="button" className="btn" onClick={MakeBTN}>
              Make
            </button>
            <button
              type="button"
              className="btn"
              onClick={() => {
                window.print();
              }}
            >
              Print
            </button>
            <button type="button" className="btn">
              Reset
            </button>
          </div>
        </div>

        {/* ----barcode labels----- */}
        <div className="barcode_preview_container">
          <div className="barcode_preview">
            <div className="barcode-logo">VS</div>
            {barcodeArray.map(item => {
              return <Barcode value={item} />;
            })}
          </div>
        </div>
      </div>
    </>
  );
};

export default BarcodeLabels;
